.hero-area{
    position: relative;
    // padding: 200px 0;
    background: url(../images/hero/hero-bg.jpg);
    background-size: cover;
    background-position: center center;
    height: 100vh;
    display: flex;
    align-items: center;
    background-repeat: no-repeat;
    @include desktop{
        height: 100%;
        padding: 150px 0;
    }
    .content{
        h6{
            font-family: $secondary-font;
            text-transform: uppercase;
            letter-spacing: 7px;
            margin-bottom: 25px;
            @include desktop{
                font-size: 16px;
                letter-spacing: 3px;
                margin-bottom: 15px;
            }
        }
        h1{
            font-size: 100px;
            line-height: 110px;
            margin-bottom: 25px;
            @include desktop{
                font-size: 60px;
                line-height: 70px;
            }
        }
        p{
            font-weight: 500;
            line-height: 30px;
            margin-bottom: 45px;
        }
        // .btn-primary{
        //     position: relative;
        //     background: transparent;
        //     overflow: hidden;
        //     &:before{
        //         content: '';
        //         position: absolute;
        //         width: 100%;
        //         height: 100%;
        //         background: $primary-light;
        //         left: 0;
        //         top: 0;
        //         z-index: -2;
        //     }
        //     &:after{
        //         content: '';
        //         position: absolute;
        //         width: 100%;
        //         height: 100%;
        //         background: $white;
        //         left: 0;
        //         top: 0;
        //         z-index: -1;
        //         transition: all .5s ease;
        //         transform: translateX(-100%);
        //     }
        //     &:hover{
        //         color: $black;
        //         &:after{
        //             transform: translateX(0);
        //             transform-origin: right;
        //         }
        //     }
        // }
    }
    .scroll-down{
        position: absolute;
        bottom: 120px;
        left: 30px;
        transform: rotate(-90deg);
        @include desktop-lg{
            left: 50%;
            bottom: 30px;
            transform: translateX(-50%) rotate(-90deg);
        }
        @include mobile{
            display: none;
        }
        a{
            position: relative;
            color: $white;
            font-size: 14px;
            text-decoration: none;
            i{
                margin-right: 15px;
            }
            &:before{
                position: absolute;
                content: '';
                background: rgba($color: $white, $alpha: .5);
                width: 54px; height: 1px;
                top: 50%;
                transition: all 1s ease;
                transform: translateY(-50%);
                right: calc(-100% - 10px);
                animation: dash-down 1s ease 1s infinite both;
            }
            @keyframes dash-down {
                0%{
                    width: 0;
                }
                100%{
                    width: 54px;
                }
            }
        }
    }
    .social-links{
        position: absolute;
        top: 50%;
        transform: translateY(-50%) rotate(-90deg);
        right: -60px;
        @include mobile{
            display: none;
        }
        ul{
            li{
                &:not(:last-child){
                    a{
                        margin-right: 10px;
                    }
                }
                a{
                    position: relative;
                    color: $white;
                    display: block;
                    padding: 0 16px;
                    text-decoration: none;
                    &::before{
                        position: absolute;
                        content: '';
                        width: 0;
                        height: 1px;
                        background: $primary-light;
                        top: 50%;
                        left: 0;
                        transform: translateY(-50%);
                        transition: all .3s ease-in-out;
                    }
                    &:hover{
                        &::before{
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
}