.navigation{
    .navbar{
        padding: 25px 50px;
        transition: all .3s ease;
        border-bottom: 1px solid rgba($color: $white, $alpha: .1);
        @include desktop{
            background: $primary-light;
        }
        .nav-item{
            &:not(:last-child){
                margin-right: 30px;
            }
        }
        .nav-link{
            font-size: 14px;
            font-weight: 500;
            color: $white;
            padding-top: 7px;
            padding-bottom: 7px;
            padding-left: 13px;
            padding-right: 13px;
        }
        &.nav-bg{
            background: $primary-light;
            padding: 20px 30px;
        }
    }
}

.navbar-nav{
    @include desktop{
        text-align: center;
    }
}