.portfolio-item{
    margin-top: 50px;
    img{
        margin-bottom: 38px;
    }
    .product-name{
        font-family: $secondary-font; font-weight: 400;
        color: #222222;
        margin-bottom: 7px;
    }
    .company{
        font-size: 14px;
    }
}

.portfolio{
    position: relative;
    .shape-medium{
        position: absolute;
        right: 0;
        bottom: -12%;
        z-index: -1;
    }
    .btn-primary{
        box-shadow: 0px 19px 32.55px 2.45px rgba($color: $black, $alpha: .08);
    }
}