.btn-primary{
    padding: 20px 45px;
    color: $black;
    border-radius: 0;
    font-family: $primary-font;
    text-transform: uppercase;
    font-weight: 700;
    outline: 0;
    box-shadow: none;
    border: none;
    position: relative;
    z-index: 1;
    background: transparent;
    overflow: hidden;
    &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: $primary-light;
        left: 0;
        top: 0;
        z-index: -2;
    }
    &:after{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: $white;
        left: 0;
        top: 0;
        z-index: -1;
        transition: all .5s ease;
        transform: translateX(-100%);
    }
    &:hover{
        color: $black;
        background: transparent;
        &:after{
            transform: translateX(0);
            transform-origin: right;
        }
    }
}

