body{
    background: $body-color;
    color: $text-color;
    overflow-x: hidden;
    // position: relative;
}

.section{
    padding: 100px 0;
}

.section-title{
    h6{
        font-family: $secondary-font;
        letter-spacing: 5px;
        color: $primary-color;
        text-transform: uppercase;
    }
    h1{
        span{
            display: inline-block;
        }
    }
}

.dividers{
    display: flex;
    align-items: center;
    width: 1170px;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    .divider{
        border-right: 1px solid #f3f3f3;
        flex-basis: 25%;
        height: 100vh;
        // opacity: .20;
        &:first-child{
            border-left: 1px solid #f3f3f3;
        }
    }
}

::-webkit-scrollbar {
    width: 5px;
    background: $white;
}

::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background: $primary-color;
} 

// Headroomjs Styles
.headroom {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: all .5s ease-in-out;
}
.headroom--unpinned {
    top: -100px;
}
.headroom--pinned {
    top: 0;
}

.dot{
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    background: rgba($color: $primary-light, $alpha: .5);
    transition: all .1s ease;
    z-index: 10000;
}